import request from "./request";
// 
let baseUrl = "http://127.0.0.1:9000/";
//newrequest
let $http = new request({
	//
	baseUrl: baseUrl,
	//
	fileUrl: baseUrl,
	// url
	defaultUploadUrl: "api/common/v1/upload_image",
	//content-type
	header: {
		'content-type': 'application/json;charset=UTF-8'
	},
	// 6000
	timeout: 6000,
	// 
	config: {
		// 
		isPrompt: true,
		// 
		load: true,
		// 
		isFactory: true
	}
});

// token
$http.getQnToken = function(callback){
	//
	$http.get("api/kemean/aid/qn_upload").then(data => {
		/*
		 *
		 *visitPrefix:
		 *token:token
		 *folderPath:
		 *region:  SCN
		 */
		callback({
			visitPrefix: data.visitPrefix,
			token: data.token,
			folderPath: data.folderPath,
			region: "SCN"
		});
	});
}
// token
$http.getAliToken = function(callback){
	//
	$http.get("api/open/v1/ali_oss_upload").then(data => {
		/*
		 *
		 *visitPrefix: 
		 *folderPath: 
		 *region:  
		 *bucket:  bucket
		 *accessKeyId: ID
		 *accessKeySecret: 
		 *stsToken: token
		 */
		callback({
			accessKeyId: data.accessKeyId,
			accessKeySecret: data.accessKeySecret,
			bucket: data.bucket,
			region: data.region,
			visitPrefix: data.visitPrefix,
			token: data.token,
			folderPath: data.folderPath,
			stsToken: data.securityToken,
		});
	});
}
//
let requestNum = 0;
//
$http.requestStart = function(options) {
	if (options.load) {
		if (requestNum <= 0) {
			//
			uni.showLoading({
				title: '',
				mask: true
			});
		}
		requestNum += 1;
	}
	// 
	if (options.method == "FILE") {
		// : options.maxSize 
		let maxSize = options.maxSize || '';
		for (let item of options.files) {
			if(item.fileType == 'image'){
				if (maxSize && item.size > maxSize) {
					setTimeout(() => {
						uni.showToast({
							title: "",
							icon: "none"
						});
					}, 500);
					return false;
				}
			} else if(item.fileType == "video"){
				if (item.duration < 3) {
					setTimeout(() => {
						uni.showToast({
							title: "3",
							icon: "none"
						});
					}, 500);
					return false;
				}
			}
		}
	}
	//token
	//options.header['token'] = "token";
	return options; // return false 
}
//
$http.requestEnd = function(options) {
	//
	if (options.load) {
		requestNum = requestNum - 1;
		if (requestNum <= 0) {
			uni.hideLoading();
		}
	}
}
//
let loginPopupNum = 0;
//
//
$http.dataFactory = async function(res) {
	console.log("", {
		url: res.url,
		resolve: res.response,
		header: res.header,
		data: res.data,
		method: res.method,
	});
	if (res.response.statusCode && res.response.statusCode == 200) {
		let httpData = res.response.data;
		if (typeof (httpData) == "string") {
			httpData = JSON.parse(httpData);
		}
		/*********()*********/

		//
		if (httpData.success || httpData.code == 200) {
			// (then)
			return Promise.resolve(httpData.data);
		} else if (httpData.code == "1000" || httpData.code == "1001" || httpData.code == 1100) {
			let content = '~';
			if (loginPopupNum <= 0) {
				loginPopupNum++;
				uni.showModal({
					title: '',
					content: content,
					confirmText: "",
					cancelText: "",
					success: function (res) {
						loginPopupNum--;
						if (res.confirm) {
							uni.navigateTo({
								url: "/pages/user/login"
							});
						}
					}
				});
			}
			// (catch)
			return Promise.reject({
				statusCode: 0,
				errMsg: "request" + (httpData.info || httpData.msg)
			});
		} else { //
			if (res.isPrompt) {
				uni.showToast({
					title: httpData.info || httpData.msg,
					icon: "none",
					duration: 3000
				});
			}
			// (catch)
			return Promise.reject({
				statusCode: 0,
				errMsg: "request" + (httpData.info || httpData.msg)
			});
		}

		/*********()*********/

	} else {
		// (catch)
		return Promise.reject({
			statusCode: res.response.statusCode,
			errMsg: "request"
		});
	}
};
// 
$http.requestError = function (e) {
	// e.statusCode === 0 
	if (e.statusCode === 0) {
		throw e;
	} else {
		uni.showToast({
			title: "",
			icon: "none"
		});
	}
}
export default $http;
